@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
.subtitle {
    font-size: 1.5rem;
}

.home-card {
    min-height: 60vh;
    margin-top: 3%;
    margin-bottom: 3%;
}

.display-card {
    background-color: rgba(0, 0, 0, 0.5);
    border: 1px solid #707070;
    border-radius: 1.3rem;
}

.card-top {
    height: 65%;
}

.card-bottom {
    height: 30%;
}

.card-button {
    width: 60%;
}

.bottom-content {
    font-size: 1.3rem;
}

.card-right {
    height: 100%;
}

.instructions {
    font-size: 1rem;
    margin-left: -8%;
}

@media(max-width:768px) {
    .home-cards {
        height: 80vh;
    }
}
.heading {
    text-align: center;
    vertical-align: middle;
}

.title-img {
    width: 30vw;
}

.heading .subtitle {
    font-size: 1.25rem;
}

@media(max-width:992px) {
    .title-img {
        width: 60vw;
    }
}

.display-card {
    background-color: rgba(0, 0, 0, 0.5);
    border: 1px solid #707070;
    border-radius: 1.3rem;
}

.display-card-body {
    overflow-y: auto;
}

.display-card-body::-webkit-scrollbar {
    width: 0.25rem;
    margin-left: -10px;
}

.display-card-body::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    opacity: 0.8;
    border-radius: 1.25rem;
}
.chat {
    height: 100%;
}

.messages {
    overflow-y: auto;
    font-size: 0.95rem;
    padding-left: 0;
    list-style: none;
    max-width: 23vw;
}

.send-message input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
}

.send-message input[type="text"]:-ms-input-placeholder {
    font-style: italic;
}

.send-message input[type="text"]::-ms-input-placeholder {
    font-style: italic;
}

.send-message input[type="text"]::placeholder {
    font-style: italic;
}

.messages::-webkit-scrollbar {
    width: 0.25rem;
    margin-left: -10px;
}

.messages::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    opacity: 0.8;
    border-radius: 1.25rem;
}

.list-row {
    border-bottom: 1px solid #707070;
    min-height: 14%;
}

.user-col {
    width: 60%;
    overflow: auto;
}

.list-username, .rank {
    font-size: 1.25rem;
}

.player-list {
    overflow-y: auto;
    overflow-x: hidden;
    height: 100%;
}

.player-list::-webkit-scrollbar {
    width: 0.25rem;
    margin-left: -10px;
}

.player-list::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    opacity: 0.8;
    border-radius: 1.25rem;
}

.playerlist-header {
    font-size: 1.4rem;
    font-weight: bold;
    border-bottom: 1px solid #707070;
}

.settings-header {
    font-size: 1.4rem;
    font-weight: bold;
    border-bottom: 1px solid #707070;
}

.rounds-text {
    margin: auto;
}
.lobby-container {
    max-width: 50vw;
}

.lobby-players {
    height: 70vh;
}

.lobby-players .rank {
    display: none;
}

@media (max-width: 768px) {
    .lobby-container {
        max-width: 80vw;
    }
}
.game-container {
    max-width: 80vw;
}

.game-container .user-col {
    width: 50%;
}

.password-card {
    height: 17vh;
    font-size: 1.25rem;
    max-width: 34vw;
}

.current-password {
    color: #f3f95e;
}

.blanks {
    font-size: 2.3rem;
    max-width: 30vw;
}

.hint-card {
    height: 32vh;
}

.hint-pretext {
    font-size: 1.25rem;
}

.current-hint {
    font-size: 2.5rem;
}

.hints {
    overflow: auto;
    margin-bottom: 5%;
    max-width: 30vw;
}

.hint-card input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
}

.hint-card input[type="text"]:-ms-input-placeholder {
    font-style: italic;
}

.hint-card input[type="text"]::-ms-input-placeholder {
    font-style: italic;
}

.hint-card input[type="text"]::placeholder {
    font-style: italic;
}

.hints::-webkit-scrollbar {
    width: 0.25rem;
    margin-left: -10px;
}

.hints::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    opacity: 0.8;
    border-radius: 1.25rem;
}

.game-chat {
    height: 70vh;
}

@media (max-width: 768px) {
    .password-card {
        max-width: 76vw;
    }
}
.timer {
    padding: 0;
    border: 1px solid #ffffff;
    border-radius: 0.8rem;
    font-size: 1.15rem;
}

.left-timer {
    width: 37%;
    border-right: 1px solid #ffffff;
}

.right-rounds {
    width: 53%;
    font-weight: bold;
}
#root, html {
	font-size: 16px;
	font-family: Lato, sans-serif;

    width: 100vw;
	height: 100vh;
    color: white !important;

	overflow-x: hidden;
	background-color: #342F43;
	background-image: url(/static/media/background.52d0b6af.png);
	background-repeat: no-repeat;
	background-size: 100%;
	background-attachment: fixed;
	background-position: center;
}

@media (min-width: 1800px) {
	#root, html {
		font-size: 20px;
	}
}

@media (min-width: 2500px) {
	#root, html {
		font-size: 40px;
	}
}

@media (max-width: 768px) {
	#root, html {
		background-image: none;
	}
}
